.page-main__name {
  margin: 29px auto 0 auto;
  padding: 0 0 0 20px;
  font-size: 36px;
  line-height: 36px;
  max-width: 410px;
  box-sizing: border-box;
  font-weight: 400;

  @media (min-width: $tablet-width) {
    font-size: 60px;
    line-height: 60px;
    padding: 54px 0 0 38px;
    margin: 0 auto;
    box-sizing: border-box;
    max-width: 708px;
  }

  @media (min-width: $desktop-width) {
    width: 100%;
    max-width: 1440px;
    box-sizing: border-box;
    margin: 65px auto 0 auto;
    padding: 0 0 0 110px;
  }
}
