.additional-products {
  position: relative;
  padding: 25px 20px 0 20px;

  @media (min-width: $tablet-width) {
    padding: 68px 30px 0 30px;
  }

  @media (min-width: $desktop-width) {
    padding: 65px 0 0 0;
    box-sizing: border-box;
    margin: 0 auto 86px auto;
  }
}

.additional-products.additional-products--line {
  &::before {
    content: "";
    position: absolute;
    top: 38px;
    right: 0;
    width: 60%;
    height: 2px;
    background: $basic_green;
    z-index: -1;
  }

  @media (min-width: $tablet-width) {
    &::before {
      top: 90px;
      width: 100%;
    }
  }

  @media (min-width: $desktop-width) {
    &::before {
      top: 99px;
    }
  }
}

.additional-products__wrapper-present {
  @media (min-width: $desktop-width) {
    display: flex;
    max-width: 1440px;
    margin: 0 auto;
    padding: 0 110px;
    box-sizing: border-box;
  }
}

.additional-products__wrapper {
  max-width: 410px;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    width: 100%;
    max-width: 708px;
    padding: 0 0 0 0;
    box-sizing: border-box;
  }

  @media (min-width: $desktop-width) {
    max-width: 895px;
    padding: 0;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }
}

.additional-products__name {
  position: relative;
  font-size: 24px;
  line-height: 24px;
  padding: 0 0 34px 0;
  margin: 0 auto;
  max-width: 410px;
  box-sizing: border-box;
  border-bottom: 1px solid #cdcdcd;
  background-color: #ffffff;

  &::after {
    content: "";
    position: absolute;
    right: 0;
    top: 13px;
    height: 2px;
    width: calc(100% - 245px);
    background-color: $basic_green;
  }

  @media (min-width: $tablet-width) {
    font-size: 36px;
    line-height: 36px;
    padding: 0 0 80px 37px;
    margin: 0 auto;
    max-width: 708px;
    border: none;

    &::after {
      top: 22px;
      width: calc(100% - 395px);
    }
  }

  @media (min-width: $desktop-width) {
    width: 100%;
    box-sizing: border-box;
    padding: 10px 110px 80px 50px;
    border-bottom: none;
    max-width: 1330px;
    margin: 0 auto;

    &::after {
      top: 34px;
      width: calc(100% - 425px);
    }
  }
}
